
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import { willClassList, targetList, levelList, dayList, hoursList, videoSrc } from '../assets/js/cikeBaiduData'
import isMobilePhone from 'validator/lib/isMobilePhone'
export default {
name: 'CikeBaidu',
directives: {
TransferDom
},
components: {
Swiper,
SwiperItem,
Step,
StepItem,
Checker,
CheckerItem,
XInput,
Group,
Popup,
XDialog,
XButton,
Alert
},
data () {
return {
swiperItemIndex: 0,
willClassList: willClassList,
willClassItem: [],
targetList: targetList,
targetItem: null,
levelList: levelList,
levelItem: null,
dayList: dayList,
dayItem: null,
hoursList: hoursList,
hoursItem: null,
isShowYuyue: false,
isShowOffer: false,
isShowOfferTwo: false,
isShowDialog: false,
isTopTips: false,
topTipsText: '',
isClassList: false,
isAlert: false,
alertTitle: '',
alertText: '',
videoSrc: videoSrc,
phoneLine: '',
iconType: '',
phoneComeSchool: '',
iconTypeComeSchool: '',
userName: '',
phonePlan: '',
buttonText: '发送验证码',
buttonDisabled: false,
checkCode: function (value) {
return {
valid: value === '1111',
msg: 'Must be 1111'
}
},
phoneCode: null
}
},
watch: {
isTopTips (val) {
if (val) {
setTimeout(() => {
this.isTopTips = false
}, 2000)
}
}
},
filters: {},
mounted () {
},
computed: {},
methods: {

getUserSave (phone, name) {
this.$http.get('http://www.cike.hk/sale/userSave', {
params: {
phone: phone,
name: name
}
}).then((data) => {
console.log(data)
}, (err) => {
console.log(err)
})
},

test (phone, name) {
console.log('用户：' + phone + '.' + name)
},
next () {
switch (this.swiperItemIndex) {
case 0:
if (this.willClassItem.length === 0) {
this.topTipsText = '您还没有选择意向课程'
this.isTopTips = true
} else { this.swiperItemIndex++ }
break
case 1:
console.log(this.targetItem)
if (this.targetItem === null) {
this.topTipsText = '您还没有选择学习目的'
this.isTopTips = true
} else { this.swiperItemIndex++ }
break
case 2:
console.log(this.levelItem)
if (this.levelItem === null) {
this.topTipsText = '您还没有选择英语水平'
this.isTopTips = true
} else { this.swiperItemIndex++ }
break
case 3:
console.log(this.dayItem, this.hoursItem)
if (this.dayItem === null || this.hoursItem === null) {
this.topTipsText = '您还没有选择每天学习时间'
this.isTopTips = true
} else { this.isShowOffer = true }
break
}
},
checkPlan (phoneCode, phone) {
if (!isMobilePhone(phone, 'zh-CN')) {
this.topTipsText = '请输入正确的电话号码'
this.isTopTips = true
} else if (!(phoneCode === '1111')) {
this.topTipsText = '请输入正确的验证码'
this.isTopTips = true
} else {
let type = '寻求报价'
let name = type + '，学习意向：' + this.willClassItem + '，学习目的：' + this.targetItem + '，目前英语水平：' + this.levelItem + '，每周学习时长：' + this.dayItem + this.hoursItem
this.test(phone, name)
this.isShowOffer = false
this.isShowOfferTwo = true
}
},
onShow () {
this.isShowYuyue = false
this.isShowOfferTwo = false
},
onHide () {

},

madePlan () {
let type = '需要人工方案'
let name = type + '，学习意向：' + this.willClassItem + '，学习目的：' + this.targetItem + '，目前英语水平：' + this.levelItem + '，每周学习时长：' + this.dayItem + this.hoursItem
this.test(this.phonePlan, name)
this.alertTitle = '感谢关注磁课英语'
this.alertText = '我们课程的顾问老师会在30分钟给你致电，请保持手机通畅'
this.isAlert = true
},

submitComeSchool (phone, userName) {
if (isMobilePhone(phone, 'zh-CN')) {
this.iconTypeComeSchool = 'success'
let type = '预约到校'
let name = userName + ',' + type
this.test(phone, name)
this.alertTitle = '恭喜你！ 预约成功'
this.alertText = '我们课程的顾问老师会在30分钟给你致电，请保持手机通畅'
this.isAlert = true
} else {
this.iconTypeComeSchool = 'error'
this.topTipsText = '请输入正确的电话号码'
this.isTopTips = true
}
},

submitLine () {
if (isMobilePhone(this.phoneLine, 'zh-CN')) {
this.iconType = 'success'
let type = '预约线下试听课'
let name = type
this.test(this.phoneLine, name)
this.alertTitle = '恭喜你！ 预约成功'
this.alertText = '我们课程的顾问老师会在30分钟给你致电，请保持手机通畅'
this.isAlert = true
} else {
this.iconType = 'error'
this.topTipsText = '请输入正确的电话号码'
this.isTopTips = true
}
},

getCode () {
this.buttonDisabled = true
var secondCount = 10
let timer = setInterval(() => {
secondCount--
this.buttonText = secondCount + 's'
if (secondCount === 0) {
clearInterval(timer)
this.buttonText = '发送验证码'
this.buttonDisabled = false
}
}, 1000)
},

playVideo (src) {
const video = this.$refs.video
video.src = src
video.play()
}
}
}



